import { FC } from 'react'
import { faker } from '@faker-js/faker'

import { MessageDialog } from '../'
import type { MessageDialogProps } from '../'

/**
 * AZ-NOTE: ommiting props from the target type makes them non-configurable
 * in the Storybook panels. It's intentional.
 */
export type MessageDialogWrapperProps = Omit<MessageDialogProps, 'isFullscreen' | 'title'> & { title?: string }

const CONTENT = faker.lorem.lines(30)

export const MessageDialogWrapper: FC<MessageDialogWrapperProps> = (props) => {
  return (
    <MessageDialog {...props}>
      {CONTENT}
    </MessageDialog>
  )
}

export const MessageDialogExampleHOC = (hocProps: MessageDialogProps): FC<MessageDialogWrapperProps> => (props) => {
  return (
    <MessageDialogWrapper {...props} {...hocProps as MessageDialogWrapperProps} />
  )
}

export const SingleButtonExample = MessageDialogExampleHOC({
  isOpen: true,
  title: 'Notification',
  onSubmit: () => alert('onSubmit called'),
  onCancel: null
})

export const DoubleButtonExample = MessageDialogExampleHOC({
  isOpen: true,
  title: 'Confirm!',
  onSubmit: () => alert('onSubmit called'),
  onCancel: () => alert('onCancel called')
})

export const CustomTitlesExample = MessageDialogExampleHOC({
  isOpen: true,
  title: 'Confirm!',
  textCancel: 'NO',
  textSubmit: 'YES',
  onSubmit: () => alert('onSubmit called'),
  onCancel: () => alert('onCancel called')
})
