import type { FC, ReactNode } from 'react'
import Button from '@UI/Button'

export interface FooterButtonsProps {
  isDisabled?: boolean
  isLoading?: boolean
  onCancel?: (() => void) | null
  onSubmit: () => void
  textSubmit?: ReactNode
  textCancel?: ReactNode
}

export const FooterButtons: FC<FooterButtonsProps> = ({
  isDisabled = false,
  isLoading = false,
  onSubmit,
  onCancel,
  textSubmit: _textSubmit,
  textCancel = 'CANCEL'
}) => {
  const isSingleButton = onCancel == null
  const textSubmit = _textSubmit ?? (isSingleButton ? 'OK' : 'SUBMIT')

  return (
    <>
      {!isSingleButton && (
        <Button
          className='cf-cancel-button'
          dataTestId='btn-cancel'
          isDisabled={isLoading}
          onClick={onCancel}
          variant='outlined'
        >
          {textCancel}
        </Button>
      )}

      <Button
        className='cf-submit-button'
        dataTestId='btn-save'
        isDisabled={isDisabled}
        isLoading={isLoading}
        onClick={onSubmit}
      >
        {textSubmit}
      </Button>
    </>
  )
}

export const FooterButtonsHOC = (props: FooterButtonsProps) => (): ReactNode => {
  return <FooterButtons {...props} />
}
