import { useMemo } from 'react'
import type { FC } from 'react'

import Modal from '@UI/Modal'
import { cn } from '@utils/ui'
import type { ModalProps } from '../Modal' // AZ-NOTE: do not replace relative import

import { FooterButtonsHOC } from './components/FooterButtons'
import type { FooterButtonsProps } from './components/FooterButtons'
import CLASS_NAME from './styles'

export type MessageDialogProps = Omit<ModalProps, 'renderFooterActions'> & Omit<FooterButtonsProps, 'isDisabled'> & {
  isSubmitDisabled?: boolean
}

export const MessageDialog: FC<MessageDialogProps> = ({
  children,
  className,
  isLoading,
  isSubmitDisabled,
  onCancel = null,
  onSubmit,
  textSubmit,
  textCancel,
  ...modalProps
}) => {
  const FooterButtons = useMemo(() => FooterButtonsHOC({
    isLoading,
    isDisabled: isSubmitDisabled,
    onCancel,
    onSubmit,
    textSubmit,
    textCancel
  }), [
    isLoading,
    isSubmitDisabled,
    onCancel,
    onSubmit,
    textSubmit,
    textCancel
  ])

  return (
    <Modal
      {...modalProps}
      className={cn(CLASS_NAME, 'cf-message-dialog', className)}
      isLoading={isLoading}
      renderFooterActions={FooterButtons}
    >
      {children}
    </Modal>
  )
}
